/**
 * ElasticPress search field component
 */

import React, { useState, useContext } from 'react';
import PropTypes from 'prop-types'; /* eslint-disable-line import/no-extraneous-dependencies */
import { replacePlaceholderInObjectValues } from '../../utils';
import { PostContext } from '../../contexts/PostContext';
import { post } from '../../api';

const SearchField = ({
	placeholder,
	value,
	name,
	minSearchCharacters,
	perPage,
	query,
	endpoint,
	hitMap,
}) => {
	const [searchValue, setSearchValue] = useState(value);
	const [, dispatch] = useContext(PostContext);

	const onChange = (event) => {
		setSearchValue(event.target.value);

		if (event.target.value.length >= minSearchCharacters) {
			dispatch({
				type: 'set_loading',
				payload: true,
			});

			let newQuery = replacePlaceholderInObjectValues(
				query,
				'%SEARCH_TERMS%',
				event.target.value,
			);

			newQuery = replacePlaceholderInObjectValues(newQuery, '%PER_PAGE%', perPage);

			post(newQuery, endpoint).then((response) => {
				let newResults = [];

				if (response.hits && response.hits.hits) {
					newResults = response.hits.hits.map(hitMap);
				}

				dispatch({
					type: 'set_posts',
					payload: newResults,
				});

				dispatch({
					type: 'set_loading',
					payload: false,
				});
			});
		} else {
			dispatch({
				type: 'set_posts',
				payload: null,
			});
		}
	};

	return (
		<input
			type="search"
			className="search-field"
			placeholder={placeholder}
			value={searchValue}
			name={name}
			onChange={onChange}
		/>
	);
};

export const query = {
	from: 0,
	size: '%PER_PAGE%',
	sort: [
		{
			_score: {
				order: 'desc',
			},
		},
	],
	query: {
		function_score: {
			query: {
				bool: {
					should: [
						{
							bool: {
								should: [
									{
										multi_match: {
											query: '%SEARCH_TERMS%',
											type: 'phrase',
											fields: [
												'post_title^8',
												'post_excerpt^1',
												'post_content^1',
												'terms.ep_custom_result.name^9999',
											],
											boost: 4,
										},
									},
									{
										multi_match: {
											query: '%SEARCH_TERMS%',
											fields: [
												'post_title^8',
												'post_excerpt^1',
												'post_content^1',
												'terms.ep_custom_result.name^9999',
											],
											type: 'phrase',
											slop: 5,
										},
									},
								],
							},
						},
					],
				},
			},
			functions: [
				{
					exp: {
						post_date_gmt: {
							scale: '14d',
							decay: 0.25,
							offset: '7d',
						},
					},
				},
			],
			score_mode: 'avg',
			boost_mode: 'sum',
		},
	},
	post_filter: {
		bool: {
			must: [
				{
					terms: {
						'post_type.raw': ['post', 'page'],
					},
				},
				{
					term: {
						post_status: 'publish',
					},
				},
			],
		},
	},
};

SearchField.defaultProps = {
	name: 's',
	placeholder: 'Search...',
	value: '',
	minSearchCharacters: 3,
	perPage: 10,
	query,
	hitMap: (hit) => {
		return hit._source;
	},
};

SearchField.propTypes = {
	name: PropTypes.string,
	value: PropTypes.string,
	placeholder: PropTypes.string,
	endpoint: PropTypes.string.isRequired,
	query: PropTypes.object,
	minSearchCharacters: PropTypes.number,
	perPage: PropTypes.number,
	hitMap: PropTypes.func,
};

export default SearchField;
